body{
    background: url('background.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
}

.laoding{
    width: 200px;
    height: 200px;
    border: 10px red ridge;
    border-right: transparent;
    border-radius: 50%;
    animation: carga 1s linear infinite;
}

.containerLoading{
    position: relative;
    top: 50%;
    left: 100%;
    transform: translate(50%, 0%);
    z-index: 100;
    width: 200px;
    height: 200px;
}

.containerLoading p{
    position: absolute;
    top: 30%;
    left: 25%;
    font-size: 30px;
}

@keyframes carga{
    0%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(360deg);
    }
}